const formatRate = (rate) => {
    const rateStr = (Math.floor(rate * 100) / 100).toString();
    const parts = rateStr.split('.');

    //    2 
    if (parts.length === 1) {
        return parts[0] + '.00';  //    .00 
    } else if (parts[1].length === 1) {
        return parts[0] + '.' + parts[1] + '0';  //   1 0 
    } else {
        return rateStr;  //  2  
    }
};

export default formatRate;